function slider({container, slide, nextArrow, previousArrow, totalCounet, currentCounter, wrapper, field}) {
    let offset = 0;
    let slideIndex = 1;

    const slides = document.querySelectorAll(slide),
        prev = document.querySelector(previousArrow),
        next = document.querySelector(nextArrow),
        total = document.querySelector(totalCounet),
        current = document.querySelector(currentCounter),
        slidesWrapper = document.querySelector(wrapper),
        width = window.getComputedStyle(slidesWrapper).width,
        slidesField = document.querySelector(field),
        slider = document.querySelector(container);

    let dots = [];

    if (slides.length < 10) {
        total.textContent = `0${slides.length}`;
        current.textContent =  `0${slideIndex}`;
    } else {
        total.textContent = slides.length;
        current.textContent =  slideIndex;
    }

    slidesField.style.width = 100 * slides.length + '%';
    slidesField.style.display = 'flex';
    slidesField.style.transition = '0.5s all';

    slidesWrapper.style.overflow = 'hidden';

    slides.forEach(slide => {
        slide.style.width = width;
    });

    slider.style.position = 'relative';

    const indicator = document.createElement('ol');
    indicator.classList.add('carousel-indicators');
    indicator.style.cssText = `position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 15;
    display: flex;
    justify-content: center;
    margin-right: 15%;
    margin-left: 15%;
    list-style: none;
    `;
    slider.append(indicator);

    for(let i=0; i<slides.length; i++) {
        const dot = document.createElement('li');
        dot.setAttribute('data-slide-to', i + 1);
        dot.style.cssText = `
        box-sizing: content-box;
        flex: 0 1 auto;
        width: 30px;
        height: 6px;
        margin-right: 3px;
        margin-left: 3px;
        cursor: pointer;
        background-color: #fff;
        background-clip: padding-box;
        border-top: 10px solid transparent;
        border-bottom: 10px solid transparent;
        opacity: .5;
        transition: opacity .6s ease;`;
        if (i==0) {
            dot.style.opacity = 1;
        }
        indicator.append(dot);
        dots.push(dot);
    }


    next.addEventListener('click', () => {
        if (offset == (+width.slice(0, width.length - 2) * (slides.length - 1))) {
            offset = 0;
        } else {
            offset += +width.slice(0, width.length - 2);
        }

        slidesField.style.transform = `translateX(-${offset}px)`;

        if (slideIndex == slides.length) {
            slideIndex = 1;
        } else {
            slideIndex++;
        }

        if (slides.length < 10) {
            current.textContent =  `0${slideIndex}`;
        } else {
            current.textContent =  slideIndex;
        }

        dots.forEach(dot => {
            dot.style.opacity='.5';
            dots[slideIndex-1].style.opacity = 1;
        });

    });

    prev.addEventListener('click', () => {
        if (offset == 0) {
            offset = +width.slice(0, width.length - 2) * (slides.length - 1);
        } else {
            offset -= +width.slice(0, width.length - 2);
        }

        slidesField.style.transform = `translateX(-${offset}px)`;

        if (slideIndex == 1) {
            slideIndex = slides.length;
        } else {
            slideIndex--;
        }

        if (slides.length < 10) {
            current.textContent =  `0${slideIndex}`;
        } else {
            current.textContent =  slideIndex;
        }

        dots.forEach(dot => {
            dot.style.opacity='.5';
            dots[slideIndex-1].style.opacity = 1;
        });

        dots.forEach(dot => {
            dot.addEventListener('click', (e) => {
                const slideTo = e.target.getAttribute('data-slide-to');

                slideIndex = slideTo;
                offset = +width.slice(0, width.length - 2) * (slideTo - 1);
                slidesField.style.transform = `translateX(-${offset}px)`;

                if (slides.length < 10) {
                    current.textContent =  `0${slideIndex}`;
                } else {
                    current.textContent =  slideIndex;
                }

                dots.forEach(dot => {
                    dot.style.opacity='.5';
                    dots[slideIndex-1].style.opacity = 1;
                });
            })
        })
    });

    /*const showIndex = (n) => {
        if (n < 10) {
            currentId.textContent = `0${n}`;
            totalId.textContent = `0${slides.length}`
        } else if (n > 10) {
            totalId.textContent = `${slides.length}`;
            currentId.textContent = `${n}`
        }
    }

    const showSlide = (n) => {
      if (n > slides.length) {
          Index = 1;
      }
      else if (n < 1) {
          Index = slides.length;
      }
        currentId.textContent='';
        totalId.textContent='';
        showIndex(Index);
      slides.forEach(item => item.style.display='none');
      slides[Index - 1].style.display='block'
    }

    showSlide(Index);

    function plusSlides(n) {
        showSlide(Index+=n);
    }

    nextSlide.addEventListener('click', () => {
        plusSlides(1);
    })
    prevSlide.addEventListener('click', () => {
        plusSlides(-1);
    })
*/

}

export default slider;